.s-solutions {
    position: relative;
    min-height: 989px;
    box-sizing: initial;
    padding: rem(150) 0 rem(155);
    overflow: visible;

    @media (max-width: 1699px) {
        min-height: 999px;
    }

    @media (max-width: 1199px) {
        min-height: 805px;
    }

    @media (max-width: 991px) {
        min-height: 612px;
        padding: rem(55) 0 rem(80);
    }

    @media (max-width: 600px) {
        min-height: 437px;
    }

    @media (max-width: 500px) {
        padding: rem(55) 0 rem(25);
    }

    .l-default {
        max-width: rem(1520);
    }

    &__title {
        font-weight: normal;
        margin-bottom: rem(45);

        @media (max-width: 600px) {
            text-align: center;
            margin-bottom: 25px;
        }
    }

    &__info {
        max-width: rem(1300);

        @media (max-width: 1699px) {
            max-width: rem(1088);
        }

        @media (max-width: 1199px) {
            max-width: rem(739);
        }

        @media (max-width: 991px) {
            max-width: rem(462);
        }

        @media (max-width: 600px) {
            max-width: 100%;
        }
    }

    li {
        font-size: rem(13);
        font-weight: 400;
        line-height: 120%;

        @media (min-width: 600px) {
            font-size: rem(15);
        }
        @media (min-width: 1024px) {
            font-size: rem(17);
        }
        @media (min-width: 1600px) {
            font-size: rem(19);
        }
    }

    p {
        margin-bottom: rem(15);
        font-size: rem(13);
        font-weight: 400;
        line-height: 170%;
        color: $grey_light_more_2;

        @media (min-width: 600px) {
            max-width: 80%;
            font-size: rem(15);
            margin-bottom: rem(15);
        }
        @media (min-width: 1024px) {
            font-size: rem(17);
            max-width: 75%;
        }
        @media (min-width: 1600px) {
            font-size: rem(19);
        }

        &:last-child {
            margin-bottom: 0;
            max-width: 100%;
            font-size: rem(13);
            font-weight: 400;
            line-height: 170%;
            color: $grey_light_more_2;
            margin-bottom: 10px;

            @media (min-width: 768px) {
                font-size: rem(15);
                margin-bottom: 0;
            }
            @media (min-width: 1024px) {
                font-size: rem(17);
                max-width: 75%;
            }
            @media (min-width: 1600px) {
                font-size: rem(19);
            }
        }
    }

    &__text {
        &--short {
            margin-top: rem(96);
            margin-left: rem(100);

            @media (max-width: 600px) {
                min-height: 437px;
                margin-top: 0;
                padding-top: rem(85);
            }

            .s-solutions__text {
                font-size: 15px;
                line-height: rem(18);
                @media (min-width: 768px) {
                    font-size: 17px;
                    line-height: 20px;
                }
                @media (min-width: 1040px) {
                    font-size: 21px;
                    line-height: 25px;
                }
            }

            @media (max-width: 1040px) {
                margin-top: rem(58);
                margin-left: rem(70);
            }

            @media (max-width: 1040px) {
                margin-left: rem(40);
            }
        }
    }

    &__img {
        position: absolute;
        top: rem(150);
        right: calc(-1826px + 30%);
        display: block;
        width: 1826px;
        height: 989px;

        @media (max-width: 1699px) {
            right: calc(-1770px + 40%);
            width: 1770px;
            height: 999px;
        }

        @media (max-width: 1199px) {
            top: unset;
            bottom: 0;
            right: calc(-1468px + 40%);
            width: 1468px;
            height: 805px;
        }

        @media (max-width: 991px) {
            right: calc(-1117px + 50%);
            width: 1117px;
            height: 612px;
        }

        @media (max-width: 600px) {
            position: static;
            width: 798px;
            height: 437px;
            transform: translate(0);
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: contain;
            object-position: left;
        }
    }
}
